.Login {
  flex: 1 1 auto;
  background-size: cover; }
  .Login .box {
    position: relative;
    top: 0;
    opacity: 1;
    float: left;
    padding: 60px 50px 40px 50px;
    width: 100%;
    background: #fff;
    border-radius: 10px;
    -webkit-transform: scale(1);
            transform: scale(1);
    z-index: 5; }
  .Login .box.back {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    top: -20px;
    opacity: .8;
    z-index: -1; }
  .Login .box:before {
    content: "";
    width: 100%;
    height: 30px;
    border-radius: 10px;
    position: absolute;
    top: -10px;
    background: rgba(255, 255, 255, 0.6);
    left: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    z-index: -1; }
  .Login .overbox .title {
    color: #fff; }
  .Login .overbox .title:before {
    background: #fff; }
  .Login .title {
    width: 100%;
    float: left;
    line-height: 46px;
    font-size: 34px;
    font-weight: 700;
    letter-spacing: 2px;
    color: #060b7c;
    position: relative; }
  .Login .title:before {
    content: "";
    width: 5px;
    height: 100%;
    position: absolute;
    top: 0;
    left: -50px;
    background: #060b7c; }
  .Login .input,
  .Login .input label,
  .Login .input input,
  .Login .input .spin,
  .Login .button,
  .Login .button button .button.login button span.fa,
  .Login .material-button .shape:before,
  .Login .material-button .shape:after,
  .Login .button.login button {
    -webkit-transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: 300ms cubic-bezier(0.4, 0, 0.2, 1); }
  .Login .material-button,
  .Login .alt-2,
  .Login .material-button .shape,
  .Login .alt-2 .shape,
  .Login .box {
    -webkit-transition: 400ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: 400ms cubic-bezier(0.4, 0, 0.2, 1); }
  .Login .input,
  .Login .input label,
  .Login .input input,
  .Login .input .spin,
  .Login .button,
  .Login .button button {
    width: 100%;
    float: left; }
  .Login .input,
  .Login .button {
    margin-top: 30px;
    height: 70px; }
  .Login .input,
  .Login .input input,
  .Login .button,
  .Login .button button {
    position: relative; }
  .Login .input input {
