    line-height: 60px; }
  .Login .button.login {
    width: 60%;
    left: 20%; }
  .Login .button.login button,
  .Login .button button {
    width: 100%;
    line-height: 64px;
    left: 0%;
    background-color: transparent;
    border: 3px solid rgba(0, 0, 0, 0.1);
    font-weight: 900;
    font-size: 18px;
    color: rgba(0, 0, 0, 0.2); }
  .Login .button.login {
    margin-top: 30px; }
  .Login .button {
    margin-top: 20px; }
