  .Login .button {
    margin: 40px 0;
    overflow: hidden;
    z-index: 2; }
  .Login .button button {
    cursor: pointer;
    position: relative;
    z-index: 2; }
  .Login .pass-forgot {
    width: 100%;
    float: left;
    text-align: center;
    color: rgba(0, 0, 0, 0.4);
    font-size: 18px; }
  .Login .click-efect {
    position: absolute;
    top: 0;
    left: 0;
    background: #5fcf9b;
    border-radius: 50%; }
  .Login .overbox {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: inherit;
    border-radius: 10px;
    padding: 60px 50px 40px 50px; }
  .Login .overbox .title,
  .Login .overbox .button,
  .Login .overbox .input {
    z-index: 111;
    position: relative;
    color: #fff !important;
    display: none; }
  .Login .overbox .title {
    width: 80%; }
  .Login .overbox .input {
    margin-top: 20px; }
  .Login .overbox .input input,
  .Login .overbox .input label {
    color: #fff; }
  .Login .overbox .material-button,
  .Login .overbox .material-button .shape,
  .Login .overbox .alt-2,
  .Login .overbox .alt-2 .shape {
    display: block; }
  .Login .material-button,
  .Login .alt-2 {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background: #5fcf9b;
    position: absolute;
    top: 40px;
    right: -45px;
    cursor: pointer;
    z-index: 100;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%); }
  .Login .material-button .shape,
  .Login .alt-2 .shape {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%; }
  .Login .material-button .shape:before,
  .Login .alt-2 .shape:before,
  .Login .material-button .shape:after,
  .Login .alt-2 .shape:after {
    content: "";
    background: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg); }
  .Login .material-button .shape:before,
  .Login .alt-2 .shape:before {
    width: 25px;
    height: 4px; }
  .Login .material-button .shape:after,
  .Login .alt-2 .shape:after {
    height: 25px;
    width: 4px; }
  .Login .material-button.active,
  .Login .alt-2.active {
    top: 50%;
    right: 50%;
    -webkit-transform: translate(50%, -50%) rotate(0deg);
            transform: translate(50%, -50%) rotate(0deg); }
  .Login .materialContainer {
    width: 100%;
    max-width: 460px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .Login *,
  .Login *:after,
  .Login *::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style-type: none;
    outline: none; }

